* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content {
  height: 80vh;
}
html,
body {
  height: 100%; /* Ensure full height for body */
  margin: 0; /* Remove default margin */
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}
.btn-custom {
  background-color: #dc3545; /* Bootstrap danger color */
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color 0.3s, transform 0.3s;
}

.btn-custom:hover {
  background-color: #c82333; /* Darker shade on hover */
  transform: translateY(-2px); /* Slight lift effect */
}

.btn-custom i {
  margin-right: 5px; /* Space between icon and text */
}

/* transaction page */

.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

/*icon css*/
.icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 1rem;
  padding: 0.5rem;
  background-color: #f7fafc; /* Light gray background */
  border-radius: 0.5rem; /* Rounded corners */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow */
}

.icon {
  margin: 0 0.75rem;
  font-size: 24px;
  color: #4a5568; /* Gray color */
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.icon:hover {
  color: #4299e1; /* Blue color on hover */
  transform: scale(1.1); /* Slightly increase size on hover */
}

/* Styles when an icon is selected */
.icon.selected {
  color: #3182ce; /* A deeper blue to indicate selection */
  border-bottom: 2px solid #3182ce; /* Underline effect */
  transform: scale(1.15); /* Slightly larger scale when selected */
}

/*pie chart*/
.analytics-card {
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  padding: 20px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease-in-out;
}

.analytics-card:hover {
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.15);
}

.card-header {
  padding: 12px;
  border-radius: 8px;
  font-weight: bold;
}

.card-body {
  padding: 20px;
}

.text-muted {
  color: #6c757d;
}

.shadow-lg {
  box-shadow: 0px 6px 16px rgba(0, 0, 0, 0.1);
}

.rounded {
  border-radius: 10px;
}

.bg-primary {
  background-color: #007bff;
}

.bg-warning {
  background-color: #ffc107;
}

.bg-danger {
  background-color: #dc3545;
}
